<template>
	<div>
	<ContentHeader title="Data Pasien" />
     <div class="content-header">
		<div class="container-fluid">
			<div class="card">
              <div class="card-header">
                <h3 class="card-title"><router-link to="/data-tindakan"><div style="#007bff">Data Pasien</div></router-link></h3>

                <div class="card-tools">
                  <button type="button" class="btn btn-tool" data-card-widget="collapse" title="Collapse">
                    <i class="fas fa-minus"></i>
                  </button>
                </div>
              </div>
              <div class="card-body" style="display: block;">
                <div class="d-flex justify-content-between align-items-center">
                    <div class="d-flex align-items-center">
                      <label class="mt-1">Cari:</label>
                      <input type="text" class="form-control form-control-sm ml-2">
                    </div>
                  </div>
                <table class="table table-bordered table-hover mt-3">
                  <thead>
                    <tr>
                      <th scope="col" class="text-sm">REKAM MEDIS</th>
                      <th scope="col" class="text-sm">NAMA</th>
                      <th scope="col" class="text-sm">NO. ID</th>
                      <th scope="col" class="text-sm">ALAMAT</th>
                      <th scope="col" class="text-sm">TELEPON</th>
                      <th scope="col" class="text-sm">GENDER</th>
                      <th scope="col" class="text-sm">AGAMA</th>
                      <th scope="col" class="text-sm">PEKERJAAN</th>
                      <th scope="col" class="text-sm">STATUS</th>
                      <th scope="col" class="text-sm">ACTION</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr v-for="row in pasien" :key="row.id">
                      <td class="text-sm" scope="row">{{ row.nomor_rekam_medis }}</td>
                      <td class="text-sm">{{ row.nama }}</td>
                      <td class="text-sm">{{ row.nomor_identitas }}</td>
                      <td class="text-sm">{{ row.alamat_lengkap }}</td>
                      <td class="text-sm">{{ row.nomor_telepon }}</td>
                      <td class="text-sm">{{ row.jenis_kelamin }}</td>
                      <td class="text-sm">{{ row.agama }}</td>
                      <td class="text-sm">{{ row.pekerjaan }}</td>
                      <td class="text-sm">{{ format_member(row.status_member) }}</td>
                      <td class="text-sm">
                      <div class="btn-group">
                              <button type="button" class="btn btn-sm btn-info text-sm">Proses</button>
                              <button type="button" class="btn btn-sm btn-info dropdown-toggle dropdown-icon" data-toggle="dropdown" aria-expanded="false">
                                <span class="sr-only"></span>
                              </button>
                              <div class="dropdown-menu" role="menu" style="">
                                <button @click="prosesKunjungan(row.id)" class="dropdown-item text-sm">Tambahkan ke kunjungan</button>
                              </div>
                            </div>
                    </td>
                    </tr>
                  </tbody>
                </table>
              <p class="text-center mt-2">{{ cekData }}</p>
              </div>
              <!-- /.card-body -->
              <div class="card-footer" style="display: block;">
                
              </div>
              <!-- /.card-footer-->
            </div>
		</div>
	</div>
    </div>
</template>

<script>
import ContentHeader from '@/components/ContentHeader'

import { ref, computed, onMounted } from 'vue'
import store from '@/store'
import axios from 'axios'

export default{
  components: {
    ContentHeader
  },
  setup(){
    const format_member = (status) => {
      if (status == 1) {
        return 'Member'
      } else {
        return 'Belum Member'
      }
    }
    const user = computed(() => store.getters['auth/user'])
    const pasien = ref()
    const cekData = ref()
    const getPasien = async () => {
      let response = await axios.get('api/pasien/get/' + user.value.cabang_id)
      
        if (response.data == 'kosong') {
          cekData.value = 'Data Masih Kosong'
          pasien.value = null
        } else {
          cekData.value = ''
          pasien.value = response.data
        }
    }

    onMounted(() => {
      getPasien()
    })

    return {
      pasien,
      cekData,
      getPasien,
      format_member
    }
  }
}
</script>